import React from "react";
import $ from 'jquery';
import "../Style/Pages/Portfolio.css"
import WebSite from "../Components/WebSite";
import mimmarImage from "../Imgs/Portfolio/mimmar.png"
import oualidPorfolioImage from "../Imgs/Portfolio/oualidPorfolio.png"
import ecmorce1 from "../Imgs/Portfolio/ecomerce1.jpg"
import ecmorce2 from "../Imgs/Portfolio/ecomerce2.jpg"
import mobileImage from "../Imgs/App-development.png"
import next from "../Imgs/next.webp"
import pevious from "../Imgs/previous.webp"
import MobileApp from "../Components/MobileApp";

let ids = [0, 1, 2]

let prevId = 0;
let nextID = 1;
let currentID = 0;
function prevClick(e) {
    if (prevId ==0){
        nextID = currentID -1
        currentID = currentID - 1;
    }else{
        prevId = prevId - 1;
        nextID = currentID -1
        currentID = currentID - 1;
    }
}

function nextClick(e) {
    prevId = currentID;
    nextID = nextID + 1
    currentID = currentID +1;
}

let technologies = "HTML, CSS, JavaScript, WordPress, PostGreSQL"
class Portfolio extends React.Component{
    constructor(props){
        super(props);


    }
    componentDidMount() {
        $(document).ready(function(){

        })
    }
    render(){
        return(
            <React.Fragment>
                <section className={"Portfolio"} id={"Portfolio"}>
                    <div className="topMenuSpacer"></div>
                    <div className="pageTitle">Few of our products</div>
                    <div className="categoriesHolder">
                        <div className="categoryHolder" id={"web"}>
                            <div className="moveLeft">
                                <a href={"#"+prevId} onClick={prevClick}><img className={"nextImage"} src={pevious} alt=""/> </a>
                            </div>
                            <div className="ProductsContainer">
                                <WebSite id={ids[0]} image={mimmarImage} name={"Mimmar Sinan"} desc={"description"} technologies={technologies} link={"www.gogle.com"}/>
                                <WebSite id={ids[1]} image={oualidPorfolioImage} name={"Oualid KHIAL Porfolio"} desc={"description"} link={"www.gogle.com"}/>
                            </div>
                            <div className="moveRight">
                                <a href={"#"+nextID} onClick={nextClick}><img className={"nextImage"} src={next} alt=""/></a>
                            </div>
                        </div>
                        <div className="categoryHolder" id={"mobile"}>
                            <MobileApp
                                id={"id"}
                                name={"E-Comerce"}
                                image1={ecmorce1}
                                image2={ecmorce2}
                                technologies={"Android, NodeJS Express, PostGreSQL "}
                            />
                        </div>

                    </div>
                    <div className="tabsMenu">
                        <div className="tab">
                            <a className={"tabEntry"} href="#web">Web Sites</a>
                        </div>
                        <div className="tab">
                            <a className={"tabEntry"} href="#mobile">Mobile applications</a>
                        </div>
                        <div className="tab">
                            <a className={"tabEntry"} href="">Desktop softwares</a>
                        </div>
                        <div className="tab">
                            <a className={"tabEntry"} href="">Other</a>
                        </div>
                    </div>

                </section>
            </React.Fragment>

        )
    }
}

export default Portfolio