import React from "react";
import {render} from "react-dom";
import "../Style/Components/NavBar.css"
import {Link} from "react-router-dom";
import burgerMenuIcon from "../Imgs/burger-menu.png"
import $ from 'jquery';
import isMobile from "../CustomLibs/Functions"


class NavBar extends React.Component{

    constructor(props){
        super(props);
    }

    componentDidMount() {
        $(document).ready(function() {

            if(isMobile){
                $('.NavBar').click(function () {

                    $('.NavBar').fadeOut("fast");
                });

            }

            $('.BurgerMenu').click(function () {

                $('.NavBar').fadeToggle("fast");
            });

        });
    }
    render(){
        return(
            <React.Fragment>
                <img className="BurgerMenu" src={burgerMenuIcon}/>
                <ul className="NavBar">
                    <li className="navLi" id="navHome">
                        <a className={"navA"} href="/#Home">Home</a>

                    </li>
                    <li className="navLi" id="navAboutMe">
                        <a className={"navA"} href="#AboutsUs">About US</a>

                    </li>
                    <li className="navLi" id="navServices">
                        <a className={"navA"} href="#Services">Services</a>
                        <Link to={"/Services"} ></Link>
                    </li>
                    <li className="navLi" id="navPortfolio">
                        <a className={"navA"} href="#Portfolio">Portfolio</a>
                        <Link to={"/portfolio"} ></Link>
                    </li>
                    <li className="navLi navApply" id="navApply">
                        <a className={"navA"} href="#Portfolio">Apply</a>
                        <Link to={"/apply"} ></Link>
                    </li>
                </ul>
            </React.Fragment>

        )
    }
}
export default NavBar